.containerContact {
  background-color: black;
  padding-top: 3rem;
  width: 100%;
  padding-bottom: 3rem;
}
.contact {
  text-align: center;
  color: whitesmoke;
  font-family: "Mosk,sans-serif";
  font-size: 4rem;
  padding-top: 2rem;
  font-family: "Mosk,sans-serif";
  font-weight: 200;
  letter-spacing: 0.1rem;
}

.imgContact {
  padding-top: 5rem;
}

.containerImgContact {
  display: flex;
  margin: 0 auto;
  padding-top: 1rem;
  width: 70%;
  border-top: solid whitesmoke 0.1rem;
  text-align: center;
  justify-content: space-evenly;
}
a {
  color: whitesmoke;
}
a:hover {
  color: gray;
}
@media only screen and (max-width: 780px) {
  .contact {
    font-size: 2rem;
  }
  .imgContact {
    padding-top: 1rem;
    width: 30%;
  }
}
