.containerProject {
  background-color: #72cada;
  display: flex;
  width: 100%;
  justify-content: center;
  padding-bottom: 10rem;
}
.card {
  border-radius: 10px;
  border: solid whitesmoke;
  text-align: center;
  margin-top: 5rem;
  color: #404040;
  width: 70%;
  height: 80%;

  background-color: aliceblue;
}
.imgProject {
  width: 100%;
  height: 50%;
  border-radius: 5px;
}
.project {
  display: flex;
  justify-content: space-around;
}

.title {
  background-color: aliceblue;
  font-family: "Open Sans";
  font-size: 1.5rem;
  color: #404040;
}
.title:hover {
  text-decoration: underline;
}
.description {
  background-color: aliceblue;
}
.aTag {
  background-color: aliceblue;
  cursor: pointer;
}
@media only screen and (max-width: 780px) {
  .project {
    display: block;
  }
  .card {
    margin: 0 auto;
    margin-top: 2rem;
    width: 60%;
    height: 15rem;
  }
  .containerProject {
    padding-bottom: 2rem;
  }
}
