* {
  background-color: black;
}
.presentation {
  display: flex;
  justify-content: space-around;
  background-color: #6fdcbf;
}

.imgProfile {
  padding-top: 1rem;
  padding-left: 3rem;
  width: 40%;
  height: 80vh;
  background-color: #6fdcbf;
}
