.name {
  padding-top: 5rem;
  text-align: center;
  font-weight: 200;
  letter-spacing: 1rem;
  font-size: 2.5rem;
  margin-right: 1rem;
  color: #404040;
  background-color: #fef8b4;
}
.text {
  background-color: #fef8b4;
  padding-bottom: 10rem;
  font-family: "Mosk,sans-serif";
}
.presentationText {
  color: gray;
  text-align: center;
  font-family: "Open Sans";
  font-weight: 200;
  letter-spacing: 0.2rem;
  font-size: 1rem;
  margin: 3rem;
  background-color: #fef8b4;
}
.profession {
  text-align: left;
  font-family: "Open Sans";
  font-weight: 200;
  letter-spacing: 1rem;
  font-size: 1rem;
  color: darkgray;
  margin-top: 2rem;
  margin-left: 3rem;
  text-align: center;
  background-color: #fef8b4;
}
@media only screen and (max-width: 780px) {
  .presentation {
    display: block;
    justify-content: center;
    padding-bottom: 0;
  }
  .text {
    padding-bottom: 2rem;
  }
  .name {
    text-align: center;
    font-size: 2rem;
    margin: 0 auto;
  }
  .profession {
    margin: 0 auto;
    text-align: center;
    padding: 1rem;
  }
  .imgProfile {
    height: 30%;
    padding-left: 25%;
    padding-bottom: 2rem;
    width: 50%;
  }
}
