.desktopMenubtn {
  color: whitesmoke;
  height: 1rem;
  border: none;
  font-family: "Open Sans";
  text-transform: uppercase;

  font-size: 1rem;
  padding: 1rem;
  margin: 0.5rem;
}
.desktopMenubtn:hover {
  cursor: pointer;
  color: #6fdcbf;
}
a {
  text-decoration: none;
}

@media only screen and (max-width: 780px) {
  .desktopMenubtn {
    font-size: 1rem;
    justify-content: center;
    padding: 0;
  }
}
