.containerImg {
  display: flex;
  justify-content: space-evenly;
  margin: 0 auto;
  text-align: center;
  width: 70%;
  background-color: #ae8fdb;
}

.title {
  text-align: center;
  color: #404040;
  font-size: 4rem;
  padding: 2rem;
  font-family: "Mosk,sans-serif";
  font-weight: 200;
  letter-spacing: 0.1rem;
  background-color: #ae8fdb;
}
.containerSkills {
  padding-top: 2rem;
  text-align: center;
  background-color: #ae8fdb;
  padding-bottom: 3rem;
}
.skillsText {
  color: #404040;
  text-align: center;
  font-family: "Open Sans";
  font-weight: 200;
  letter-spacing: 0.2rem;
  font-size: 1rem;
  margin: 3rem;
  background-color: #ae8fdb;
  padding-bottom: 3rem;
}
@media only screen and (max-width: 780px) {
  .containerImg {
    display: block;
  }
  .title {
    font-size: 2rem;
  }
}
