.portfolioTitle {
  text-align: center;
  color: #404040;
  font-size: 4rem;
  padding-top: 2rem;
  font-family: "Mosk,sans-serif";
  font-weight: 200;
  letter-spacing: 0.1rem;
  background-color: #72cada;
  margin: 0;
  padding: 2rem;
}
.portfolioText {
  color: #404040;
  text-align: center;
  font-family: "Open Sans";
  font-weight: 200;
  font-size: 1rem;
  letter-spacing: 0.2rem;
  padding-bottom: 3rem;
  padding-top: 2rem;
  background-color: #72cada;
  margin: 0;
}
@media only screen and (max-width: 780px) {
  .portfolioTitle {
    font-size: 2rem;
  }
}
